.burgerMenuIcon {
    width: 34px;
    height: 34px;
}


.headerWrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background-color: #FFFFFF;
    box-shadow: 0 2px 5px #000;
    z-index: 10;
}

.header {
    padding: 15px 50px 15px 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header h1 {
    font-size: 34px;
    font-weight: 700;
}



